















































import { Component, Prop, Vue } from "vue-property-decorator";
import { AxiosResponse } from "axios";
import axios from "axios";
import { mangaList, MangaIO } from "@/datatypes";

@Component
export default class Card1 extends Vue {
  myList: Array<mangaList> = [];
  searchTerm = "";
  searchNum = 0;
  quotableAPI = "https://api.jikan.moe/v3";

  mounted(): void {
    this.mangaSearch();
  }
  mangaSearch(): void {
    console.log(this.searchTerm);
    axios
      .request({
        method: "GET",
        //url: `https://api.jikan.moe/v4/anime`,
        url: `${this.quotableAPI}/search/manga`,
        params: {
          q: this.searchTerm,
          page: this.searchNum,
        },
      })
      .then((r: AxiosResponse) => {
        console.log(this.searchTerm);
        return r.data;
      })
      .then((r: MangaIO) => {
        this.myList.splice(0);
        this.myList.push(...r.results);
      });
  }
  
}
